import {url} from "/ts/util/utils";
import opg from "/ts/opg";

const page = __uri('./detail.html');

export default class ViewDetail{
	constructor(id , winTitle){
		let src = url.setParam(page, {id});
		opg.popTop(`<iframe src="${src}"></iframe>` , {
			title: winTitle,
			height: 600,
			width: 800,
			btnMax: true,
		});
	}
}